import { Component, h, Prop, Element } from '@stencil/core';
import priceFormat from '../../global/functions/price';


@Component({
  tag: 'ks-cart-summary',
})
export class CartSummary {

    @Prop({reflect: true}) text: string;
    @Prop() price: number;
    @Prop() large: boolean;
    @Prop() emphasis: boolean = false;

    @Element() root: HTMLElement;

    componentDidLoad() {
        this.root.style.display = "block";
    }

    render() {
        const price = priceFormat(this.price);
        const leaderOptions = "media: 500; fill: .";

        if (this.large){
            const largeStyle = {
                fontSize: "24px",
                fontWeight: "700",
                color: this.emphasis ? "#e21334" : ""
            }
            
            return(
                <div class="uk-grid-small uk-flex-bottom uk-margin-small-bottom" style={{fontSize: "18px"}} uk-grid>
                    <div class="uk-width-expand" uk-leader={leaderOptions}><span class="ks-text-decorated" style={largeStyle}>{this.text}</span></div>
                    <div class="ks-text-decorated" style={largeStyle}>{price}</div>
                </div>
            )
        }

        else {
            const normalStyle = {
                fontWeight: this.emphasis ? "700" : "",
                color: this.emphasis ? "#e21334" : ""
            }
            
            return(
                <div class="uk-grid-small uk-flex-bottom uk-margin-small-bottom" style={{fontSize: "18px"}} uk-grid>
                    <div class="uk-width-expand" uk-leader={leaderOptions} style={normalStyle}>{this.text}</div>
                    <div style={normalStyle}>{price}</div>
                </div>
            );
        }
    }
}